import { Component } from "@angular/core";
import { NGXLogger } from "ngx-logger";
import { AdminApiService } from "src/app/api";

@Component({
  selector: "app-admin",
  templateUrl: "./admin.page.html",
  styleUrls: ["./admin.page.scss"],
})
export class AdminPage {
  public constructor(
    private readonly adminApiService: AdminApiService,
    private readonly logger: NGXLogger,
  ) {}

  public async doMergeIngredients(
    primaryId: string,
    secondaryId: string,
  ): Promise<void> {
    this.logger.info(`Mirging ingredients ${primaryId} and ${secondaryId}`);

    const result = await this.adminApiService
      .adminMergeIngredients({ primaryId, secondaryId })
      .toPromise();
    alert(result);
  }
}
